@import "../../shared/styles/variables";

.about {
  display: flex;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
  margin-top: 20px;

  .aboutText {
    margin: 0 50px 0 50px;
    text-align: justify;

    @media (max-width: 450px) {
      font-size: 14px;
    }
  }

  h2 {
    font-family: "Salsa";
    font-size: 30px;
    margin: 0;
  }

  .aboutText p {
    font-family: "Salsa";
    font-size: 18px;
  }

  .goHome {
    margin-top: 20px;
    text-decoration: underline;
    color: $color-darkblue;
    margin-bottom: 20px;
    &:hover {
      color: #d8af69;
    }
  }
}
